<template>
    <ul class="nav nav-tabs">
        <template v-if="instances.length">
            <li style="width: 100%; border: 1px solid #ddd; position: relative" v-for="instance in instances">
                <a :href="config.baseURL + ':' + instance.port" target="_blank" style="text-align: left">
                    <i class="fa fa-lg" :class="!instance.is_up ? 'fa-minus-circle' : 'fa-check-circle-o'" :style="{color: !instance.is_up ? '#ED1C24' : '#46a546'}" v-if="instance.is_ready === '1'"></i>
                    <img src="img/ajax-loader.gif" style="display: inline; width: 16px" v-else>
                    &nbsp;
                    <strong>#{{ instance.id}} {{ instance.name }} </strong>
                    <template v-if="instance.branch_name">
                        <span>[BRANCH]</span>
                        <span>[{{ instance.branch_name }}]</span>
                    </template>
                    <template v-else>
                        <span>[TAG]</span>
                        <span>[{{ instance.tag_prefix + '.' + instance.tag_revision }}]</span>
                    </template>
                    <span v-if="instance.is_persistant === '1'">[P]</span>
                    <span v-if="instance.refresh_period > 0">[{{ instance.refresh_period + ' min' }}]</span>
                    <span>[{{ getUserName(instance.user.email) }}]</span>
                </a>
                <div style="right: 10px;bottom: 10px;position: absolute;">
                    <a href="javascript:alert('URL copied!')" class="copy-link" title="Copy instance url to clipboard" :data-clipboard-text="config.baseURL + ':' + instance.port"> <i class="fa fa-clipboard fa-lg" aria-hidden="true"></i> Copy URL </a>
                </div>
            </li>
        </template>
        <template v-else>
            <li>
                No instances available.
            </li>
        </template>
    </ul>
</template>

<script>
    import config from '../../config';
    import Helpers from '../../Helpers';

    export default {
        props: ['sectionTitle'],
        data() {
            return {
                config,
                instances: [],
                timerId: null
            }
        },
        created() {
            this.loadInstances()
                .stopAutoRefresh()
                .startAutoRefresh();
        },
        destroyed() {
            this.stopAutoRefresh();
        },
        methods: {
            loadInstances() {
                this.$http.get(this.config.endpoints.getDashboard)
                    .then(response => {
                        return response.json();
                    })
                    .then(data => {
                        this.instances = data;
                    });
                return this;
            },
            startAutoRefresh() {
                let refreshInstances = () => this.loadInstances(),
                    that = this;

                this.timerId = setTimeout(function tick() {
                    refreshInstances();
                    that.timerId = setTimeout(tick, config.defaultRefreshInterval);
                }, config.defaultRefreshInterval);

                return this;
            },
            stopAutoRefresh() {
                if (this.timerId) {
                    clearTimeout(this.timerId);
                }

                return this;                                               
            },
            getUserName(email) {
                let arr = email.split('@');
                return arr[0];
            }
        }
    }
</script>


