let baseURL = window.Buildkiter.proto + '://' + window.Buildkiter.host;
//let baseApiURL = baseURL + '/api/v1';
let baseApiURL = 'http://laravel.local/admin';

export default {
    baseURL,
    baseApiURL,
    endpoints: {
        getDashboard: baseApiURL + '/dashboard',
        getRepos: baseApiURL + '/posts',
        getReposDelete: baseApiURL + '/posts',
        postsAddRepo: baseApiURL + '/posts',
        postsEditRepo: baseApiURL + '/posts',
        uploadGuide: baseApiURL + '/uploadGuide',
        instance: {
            start: baseApiURL + '/instances/start',
            stop: baseApiURL + '/instances/stop',
            delete: baseApiURL + '/instances/delete',
            refresh: baseApiURL + '/instances/refresh',
        }
    },
    sectionTitles: {
        mainDashboard: 'List of instances',
        repoList: 'List of repos',
        repoAdd: 'Add new post',
        uploadGuide: 'Upload new Sketches'
    },
    sectionSettings: {
        mainDashboard: {
            title: 'List of instances',
            showServerStatus: true
        },
        repoList: {
            title: 'List of posts',
            addButton: {
                title: '+ Add post',
                routeName: 'repoAdd'
            }
        },
        instanceList: {
            title: 'List of instances',
            addButton: {
                title: '+ Add instance',
                routeName: 'instanceAdd'
            }
        },
        repoAdd: {
            title: 'Add new post',
        },
        instanceAdd: {
            title: 'Add repository',
        },
        uploadGuide: {
            title: 'Upload new Sketches'
        }
    },
    defaultRefreshInterval: 3000,
    serverStatusEnabled: false,
    sketchesType: [{
            name: 'common',
            description: 'Common Sketches'
        },
        {
            name: 'checkout',
            description: 'Ag-1486 (Checkout) Sketches'
        }
    ]
}


