import config from './config';

export default {
    makeURL(path) {
        let _path = path || '';
        return config.baseURL + '/' + _path.split('.').join('/');
    },
    getAppVersion() {
        return window.Buildkiter.version;
    },
    showOverlay(_selector) {
        var selector = _selector || document,
            $el = $(selector),
            offset = $el.offset(),
            width = $el.outerWidth(),
            height = $el.outerHeight();

        $('#overlay').css({
            'width': width,
            'height': height,
            'display': 'table',
            'left': offset.left,
            'top': offset.top
        });
    },
    hideOverlay() {
        $('#overlay').css({
            'width': 0,
            'height': 0,
            'display': 'none',
            'left': 0,
            'top': 0
        });
    }
}


