<template>
    <ul class="nav nav-tabs">
    <template v-if="repos.length">
            <li style="width: 100%; border: 1px solid #ddd; position: relative" v-for="repo in repos" :key="repo.id">
                <a :href="repo.url" target="_blank" class="row-text-line" style="text-align: left">
                    <i class="fa fa-check-circle-o fa-lg" style="color: #46a546" v-if="repo.is_ready != '1'"></i>
                    <img src="img/ajax-loader.gif" style="display: inline; width: 16px" v-else>                   
                    <strong>#[{{ repo.id }}]</strong>&nbsp;&nbsp;<strong>{{ repo.title }}</strong> [{{ repo.views_count }}]
                </a> 
                <div style="right: 10px;bottom: 10px;position: absolute;">
                    <a href="javascript:;" title="Delete repo" style="color: red" @click.stop.prevent="deleteRepo(repo.id)">
                        <i class="fa fa-trash-o fa-lg" aria-hidden="true" ></i> Delete
                    </a>
                </div>
            </li>
        </template>
        <template v-else>
            <li>
                No posts available.
            </li>
        </template>        
    </ul>
</template>

<script>
    import config from '../../config';
    import Helpers from '../../Helpers.js';

    export default {
        props: [],
        data() {
            return {
                config,
                Helpers,
                repos: [],
                timerId: null
            }
        },
        created() {
            this.loadRepos()
                .stopAutoRefresh()
                .startAutoRefresh();
        },
        destroyed() {
            this.stopAutoRefresh();
        },
        methods: {
            loadRepos() {
                this.$http.get(this.config.endpoints.getRepos)
                    .then(response => {
                        return response.json();
                    })
                    .then(data => {
                        this.repos = data;
                    });
                   
                return this;                    
            },
            startAutoRefresh() {           
                let refreshRepos = () => this.loadRepos(),
                    that = this;

                this.timerId = setTimeout(function tick() {
                    refreshRepos();
                    that.timerId = setTimeout(tick, config.defaultRefreshInterval);
                }, config.defaultRefreshInterval);

                return this;                          
            },
            stopAutoRefresh() {
                if (this.timerId) {
                    clearTimeout(this.timerId);
                }

                return this;
            },
            deleteRepo(repoId) {
                if (confirm('Are you sure to delete this post?')) {
                    let repo = this.repos.find(function(element, index, array) {
                        if (element.id === repoId) {
                            return true;
                        }
                    });

                    this.stopAutoRefresh();
                    //repo.is_ready = '0';
                    this.$http.delete(this.config.endpoints.getReposDelete + '/' + repoId)
                        .then(response => {
                            return response.json();
                        })
                        .then(data => {
                            this.loadRepos();
                            this.startAutoRefresh();
                        });

                    return this;                                        
                }
            },
        }
    }
</script>


