<template>
  <div id="app">
      <div class="main-wrapper">
          <div class="content-wrapper">
            <div class="componentWrapper">
                <BlockHeader></BlockHeader>
                <div class="container">
                  <AddButton v-if="showAddButton">
                    <router-link class="btn btn-primary" :to="{ name: addButtonSettings.to }" exact>{{ addButtonSettings.title }}</router-link>        
                  </AddButton>
                  <BlockWall :sectionTitle="sectionTitle"></BlockWall>
                  <BlockServerInfo v-if="showServerStatus"></BlockServerInfo>
                </div>
            </div>        
          </div>
          <BlockFooter :appVersion="appVersion"></BlockFooter>
      </div>
      <div id="overlay" class="overlay">
          <div class="loader">
              <img src="/img/loader.gif">
          </div>
      </div>
  </div>
</template>

<script>
  import BlockHeader from './components/Block/Header.vue';
  import BlockFooter from './components/Block/Footer.vue';
  import BlockWall from './components/Block/Wall.vue';
  import BlockServerInfo from './components/Main/ServerInfo.vue';
  import AddButton from './components/Common/AddButton.vue';
  import config from './config';

  export default {
    watch: {
        $route: (route) => {}
    },
    computed: {
        showServerStatus: function() {
            return config.sectionSettings[this.$route.name].showServerStatus ? config.serverStatusEnabled : false;
        },     
       sectionTitle() {
         return config.sectionSettings[this.$route.name].title;
       },
       showAddButton() {
          return config.sectionSettings[this.$route.name].addButton ? true : false;
       },
       addButtonSettings() {
         return {
           to: config.sectionSettings[this.$route.name].addButton ? config.sectionSettings[this.$route.name].addButton.routeName : '',
           title: config.sectionSettings[this.$route.name].addButton ? config.sectionSettings[this.$route.name].addButton.title : ''
         }
       }
    },
    data () {
      return {
        appVersion: window.Buildkiter.version
      }
    },
    components: {
      BlockHeader,
      BlockFooter,
      BlockWall,
      BlockServerInfo,
      AddButton
    }
  }
</script>

<style lang="scss">
  #app {
    height: 100%;
  }
</style>



