<template>
    <ul class="nav nav-tabs">
        <template v-if="instances.length">
            <li style="width: 100%; border: 1px solid #ddd; position: relative" v-for="instance in instances">
                <a :href="config.baseURL + ':' + instance.port" target="_blank" style="text-align: left">
                    <i class="fa fa-lg" :class="!instance.is_up ? 'fa-minus-circle' : 'fa-check-circle-o'" :style="{color: !instance.is_up ? '#ED1C24' : '#46a546'}" v-if="instance.is_ready === '1'"></i>
                    <img src="img/ajax-loader.gif" style="display: inline; width: 16px" v-else>
                    &nbsp;
                    <strong>#{{ instance.id}} {{ instance.name }} </strong>
                    <template v-if="instance.branch_name">
                        <span>[BRANCH]</span>
                        <span>[{{ instance.branch_name }}]</span>
                    </template>
                    <template v-else>
                        <span>[TAG]</span>
                        <span>[{{ instance.tag_prefix + '.' + instance.tag_revision }}]</span>
                    </template>
                    <span v-if="instance.is_persistant === '1'">[P]</span>
                    <span v-if="instance.refresh_period > 0">[{{ instance.refresh_period + ' min' }}]</span>
                    <span>[{{ getUserName(instance.user.email) }}]</span>
                </a>
                <div style="right: 330px;bottom: 10px;position: absolute;">
                    <a href="javascript:alert('URL copied!')" class="copy-link" title="Copy instance url to clipboard" :data-clipboard-text="config.baseURL + ':' + instance.port">
                        <i class="fa fa-clipboard fa-lg" aria-hidden="true"></i> Copy URL
                    </a>
                </div>
                <div style="right: 260px;bottom: 10px;position: absolute;">
                    <a href="javascript:;" @click="startInstance(instance.id)">
                        <span style="font-size: 1.5rem"><i class="fa fa-play-circle-o fa-lg"></i> Start</span>
                    </a>
                </div>
                <div style="right: 160px;bottom: 10px;position: absolute;">
                    <a href="javascript:;" @click="refreshInstance(instance.id)">
                        <span style="font-size: 1.5rem"><i class="fa fa-refresh fa-lg"></i> Refresh</span>
                    </a>
                </div>
                <div style="right: 90px;bottom: 10px;position: absolute;">
                    <a href="javascript:;" @click="stopInstance(instance.id)">
                        <span style="font-size: 1.5rem"><i class="fa fa-stop-circle-o  fa-lg"></i> Stop</span>
                    </a>
                </div>
                <div style="right: 10px;bottom: 10px;position: absolute;">
                    <a role="button" @click="deleteInstance(instance.id)">
                        <span style="font-size: 1.5rem"><i class="fa fa-trash-o fa-lg"></i> Delete</span>
                    </a>
                </div>
            </li>
        </template>
        <template v-else>
            <li>
                No instances available.
            </li>
        </template>        
    </ul>   
</template>

<script>
    import config from '../../config';
    import Helpers from '../../Helpers.js';

    export default {
        props: [],
        data() {
            return {
                config,
                Helpers,
                instances: [],
                timerId: null
            }
        },
        created() {
            this.loadInstances()
                .stopAutoRefresh()
                .startAutoRefresh();
        },
        destroyed() {
            this.stopAutoRefresh();
        },
        methods: {
            startInstance(id) {
                if (confirm('Are you sure to start this instance?')) {
                    let instance = this.getInstanceById(id);
                    instance.is_ready = '0';

                    this.$http.get(this.config.endpoints.instance.start + '/' + id)
                        .then(response => {
                            return response.json();
                        })
                        .then(data => {
                            this.showToaster(true);
                            console.log(data);
                        });
                }
            },
            stopInstance(id) {
                if (confirm('Are you sure to stop this instance?')) {
                    let instance = this.getInstanceById(id);
                    instance.is_ready = '0';

                    this.$http.get(this.config.endpoints.instance.stop + '/' + id)
                        .then(response => {
                            return response.json();
                        })
                        .then(data => {
                            this.showToaster(true);
                            console.log(data);
                        });
                }
            },
            refreshInstance(id) {
                if (confirm('Are you sure to refresh this instance?')) {
                    let instance = this.getInstanceById(id);
                    instance.is_ready = 0;

                    this.$http.get(this.config.endpoints.instance.refresh + '/' + id)
                        .then(response => {
                            return response.json();
                        })
                        .then(data => {
                            //this.instances = data;
                            console.log(data);
                            this.showToaster(true);
                        });
                }
            },
            loadInstances() {
                this.$http.get(this.config.endpoints.getDashboard)
                    .then(response => {
                        return response.json();
                    })
                    .then(data => {
                        this.instances = data;
                    });
                return this;
            },
            showToaster(result) {
                if (result) {
                    setTimeout(() => {
                        $.toast({icon: 'success', text: "Instance was deleted"});
                    }, 1500);
                } else {
                    setTimeout(() => {
                        $.toast({icon: 'error', text: "Instance was not deleted"});
                    }, 1500);
                }

                return this;
            },
            startAutoRefresh() {
                let refreshInstances = () => this.loadInstances(),
                    that = this;

                this.timerId = setTimeout(function tick() {
                    refreshInstances();
                    that.timerId = setTimeout(tick, config.defaultRefreshInterval);
                }, config.defaultRefreshInterval);

                return this;
            },
            stopAutoRefresh() {
                if (this.timerId) {
                    clearTimeout(this.timerId);
                }

                return this;                                               
            },
            deleteInstance(instanceId) {
                if (confirm('Are you sure to delete this instance?')) {
                    let instance = this.instances.find(function(element, index, array) {
                        if (element.id === instanceId) {
                            return true;
                        }
                    });

                    this.stopAutoRefresh();
                    instance.is_ready = '0';
                    this.$http.get(this.config.endpoints.getInstanceDelete + '/' + instanceId)
                        .then(response => {
                            return response.json();
                        })
                        .then(data => {
                            this.loadInstances()
                                .startAutoRefresh()
                                .showToaster(data.result === 'OK');
                        });

                    return this;                                        
                }
            },
            getUserName(email) {
                let arr = email.split('@');
                return arr[0];
            },
            getInstanceById(id) {
                return this.instances.find(function(element, index, array) {
                    if (element.id === id) {
                        return true;
                    }
                });
            }
        }
    }
</script>


