<template>
    <form class="form-horizontal" method="post" @submit.prevent.stop="formSubmit">
        <div class="form-group" :class="{ 'has-error': errors.has('title') || (hasValidationErrors && validationErrors.title) }">
            <label for="inputEmail3" class="col-sm-2 control-label">Post title</label>
            <div class="col-sm-8">
                <input type="text" class="form-control" value="" name="title" placeholder="Title of post" v-model="repoData.title" v-validate="'required'">
                <p class="text-danger" v-if="errors.has('title')">{{ errors.first('title') }}</p>
                <p class="text-danger" v-if="hasValidationErrors && validationErrors.title">{{ validationErrors.title[0] }}</p>
            </div>
        </div>
        <div class="form-group" :class="{ 'has-error': errors.has('announce') || (hasValidationErrors && validationErrors.announce) }">
            <label for="inputPassword3" class="col-sm-2 control-label">Post announce</label>
            <div class="col-sm-8">
                <textarea class="form-control" name="announce" v-model="repoData.announce" v-validate="'required|min:10'"></textarea>
                <p class="text-danger" v-if="errors.has('announce')">{{ errors.first('announce') }}</p>
                <p class="text-danger" v-if="hasValidationErrors && validationErrors.announce">{{ validationErrors.announce[0] }}</p>
            </div>
        </div>
        <div class="form-group" :class="{ 'has-error': errors.has('image') || (hasValidationErrors && validationErrors.image) }">
            <label for="inputPassword3" class="col-sm-2 control-label">Post image URL</label>
            <div class="col-sm-8">
                <input type="text" class="form-control" value="" name="image" placeholder="Link to image URL" v-model="repoData.image" v-validate="'url'">
                <p class="text-danger" v-if="errors.has('image')">{{ errors.first('image') }}</p>
                <p class="text-danger" v-if="hasValidationErrors && validationErrors.image">{{ validationErrors.image[0] }}</p>
            </div>
        </div>
        
        <div class="form-group">
            <div class="col-sm-offset-2 col-sm-8">
                <button type="submit" class="btn btn-success">Add</button>&nbsp;&nbsp;
                <router-link to="/repos" class="btn btn-default" exact>Cancel</router-link>
            </div>
        </div>
    </form>
</template>

<script>
    import config from '../../config';

    export default {
        props: [],
        data() {
            return {
                config,
                repoData: {
                    title: '',
                    announce: '',
                    image: ''
                },
                hasValidationErrors: false,
                validationErrors: [],
                submitted: false
            }
        },
        created() {

        },
        methods: {
            formSubmit() {
                this.$validator.validateAll().then(() => {
                    this.$http.post(this.config.endpoints.postsAddRepo, this.repoData)
                        .then(response => {
                            this.hasValidationErrors = false;

                            return response.json();
                        }, response => {
                            this.hasValidationErrors = true;

                            return response.json();
                        })
                        .then(data => {
                            if (this.hasValidationErrors) {
                                this.validationErrors = data;
                                return;
                            }
                            this.$router.push({name: 'repoList'});
                        });
                }, (data) => {
                    //Validation failed
                });
            }
        }
    }
</script>


