<template>
    <form method="post" class="form-horizontal" enctype="multipart/form-data" @submit.prevent.stop="formSubmit">
        <div class="form-group" :class="{'has-error': errors.has('guide')}">
            <label for="inputEmail3" class="col-sm-2 control-label">ZIP archive with sketches</label>
            <div class="col-sm-8">
                <input type="file" class="form-control" name="guide" v-on:change="onFileChange($event)" v-validate="'required|ext:zip'">
                <p class="text-danger" v-if="errors.has('guide')">{{ errors.first('guide') }}</p>
            </div>
        </div>

        <div class="form-group" :class="{'has-error': errors.has('upload_type')}">
            <label for="inputEmail3" class="col-sm-2 control-label">Type of Sketch</label>
            <div class="col-sm-8">
                <select name="upload_type" v-model="uploadType" class="form-control" v-validate="'required'">
                    <option value="" disabled>Choose from list</option>
                    <option :value="sketchType.name" v-for="sketchType in config.sketchesType">{{ sketchType.description }}</option>
                </select>
                <p class="text-danger" v-if="errors.has('upload_type')">{{ errors.first('upload_type') }}</p>
            </div>
        </div>    

        <div class="form-group">
            <div class="col-sm-offset-2 col-sm-8">
                <button type="submit" class="btn btn-success">Upload</button>&nbsp;&nbsp;
                <a href="#/" class="btn btn-default">Cancel</a>
            </div>
        </div>
    </form>
</template>

<script>
    import config from '../config';
    import Helpers from '../Helpers';

    export default {
        data() {
            return {
                config,
                formData: new FormData(),
                uploadType: ''
            }
        },
        methods: {
            onFileChange(e) {
                this.formData.append('guide', e.target.files[0]);
            },
            formSubmit() {
                this.$validator.validateAll().then(() => {
                    this.formData.append("upload_type", this.uploadType);
                    Helpers.showOverlay('.panel .panel-body');

                    this.$http.post(this.config.endpoints.uploadGuide, this.formData)
                        .then(response => {
                            return response.json();
                        }, response => {
                            return response.json();
                        })
                        .then(data => {
                            Helpers.hideOverlay();

                            if (data.result === 'OK') {
                                this.$router.push({name: 'mainDashboard'});
                                setTimeout(() => {
                                    alert('File successfully uploaded!');
                                }, 500);                                
                                
                            } else {
                                alert('An error occurred during upload!');
                            }
                        });                    
                }, (data) => {
                    //Validation failed
                });
            }
        }
    }
</script>


