import MainDashboard from './components/Main/Dashboard.vue';
import RepoList from './components/Repo/List.vue';
import InstanceList from './components/Instance/List.vue';
import InstanceAdd from './components/Instance/Add.vue';
import RepoAdd from './components/Repo/Add.vue';
import UploadGuide from './components/UploadGuide.vue';

export const routes = [{
        path: '/posts/add',
        component: RepoAdd,
        name: 'repoAdd'
    },
    {
        path: '/posts',
        component: RepoList,
        name: 'repoList'
    },
    {
        path: '',
        component: MainDashboard,
        name: 'mainDashboard'
    },
];


