<template>
      <nav class="navbar navbar-default navbar-static-top">
          <div class="container">
              <div class="navbar-header">
                  <!-- Collapsed Hamburger --><button type="button" class="navbar-toggle collapsed" data-toggle="collapse" data-target="#app-navbar-collapse">
                  <span class="sr-only">Toggle Navigation</span>
                  <span class="icon-bar"></span>
                  <span class="icon-bar"></span>
                  <span class="icon-bar"></span>
              </button>
                  <!-- Branding Image --><router-link class="navbar-brand" to="/">Blog Admin</router-link>
              </div>
              <div class="collapse navbar-collapse" id="app-navbar-collapse">
                  <!-- Left Side Of Navbar -->
                  <ul class="nav navbar-nav main-navbar">
                      <router-link tag="li" class="menu-dashboard" to="/" exact><a>Dashboard</a></router-link>
                      <router-link tag="li" class="menu-repos" to="/posts"><a>Posts</a></router-link>
                  </ul>
                  <!-- Right Side Of Navbar -->
                  <!--<ul class="nav navbar-nav navbar-right">
                      <li><router-link to="/login">Login / Register</router-link></li>
                  </ul>-->
              </div>
          </div>
      </nav>
</template>

<script>
    let EventListener = {
    /**
    * Listen to DOM events during the bubble phase. (by vue-strap)
    *
    * @param {DOMEventTarget} target DOM element to register listener on.
    * @param {string} eventType Event type, e.g. 'click' or 'mouseover'.
    * @param {function} callback Callback function.
    * @return {object} Object with a `remove` method.
    */
    listen (target, eventType, callback) {
        if (target.addEventListener) {
            target.addEventListener(eventType, callback, false);
            return {
                remove () {
                    target.removeEventListener(eventType, callback, false);
                }
            }
        } else if (target.attachEvent) {
            target.attachEvent('on' + eventType, callback)           
            return {
                remove () {
                    target.detachEvent('on' + eventType, callback)
                }
            }
        }
    }
}

export default {
    mounted() {
        const dropdowns = document.querySelectorAll('.dropdown > .dropdown-toggle');

        if (dropdowns) {
            for (var i = 0; i < dropdowns.length; i++) {
                this.removeEvents.push(EventListener.listen(dropdowns[i], 'click', (e) => {
                    e.preventDefault();
                    e.target.offsetParent.classList.add('open');
                }));
            }
        }        

        this.removeEvents.push(EventListener.listen(window, 'click', (e) => {
            if (e.target.classList.contains('dropdown-toggle')) {
                return;
            }

            const dropdowns = document.querySelectorAll('.dropdown');

            for (let i = 0; i < dropdowns.length; i++) {
                dropdowns[i].classList.remove('open');
            }
        }));
    },
    beforeDestroy () {
        if (this.removeEvents) {
            this.removeEvents.forEach((item) => {
                item.remove();
            });
        }
    },    
    data() {
        return {
            removeEvents: []
        }
    }
}

</script>


