<template>
    <form class="form-horizontal" method="post" @submit.prevent.stop="formSubmit">
        <div class="form-group" :class="{ 'has-error': errors.has('name') || (hasValidationErrors && validationErrors.name) }">
            <label for="inputEmail3" class="col-sm-2 control-label">Repo name</label>
            <div class="col-sm-8">
                <input type="text" class="form-control" value="" name="name" placeholder="R4_16" v-model="repoData.name" v-validate="'required'">
                <p class="text-danger" v-if="errors.has('name')">{{ errors.first('name') }}</p>
                <p class="text-danger" v-if="hasValidationErrors && validationErrors.name">{{ validationErrors.name[0] }}</p>
            </div>
        </div>
        <div class="form-group" :class="{ 'has-error': errors.has('url') || (hasValidationErrors && validationErrors.url) }">
            <label for="inputPassword3" class="col-sm-2 control-label">Stash repository URL</label>
            <div class="col-sm-8">
                <input type="text" class="form-control" value="" name="url" placeholder="https://stash.mvideo.ru/stash/scm/mvidatgnr/r4_16.git" v-model="repoData.url" v-validate="'required|url'">
                <p class="text-danger" v-if="errors.has('url')">{{ errors.first('url') }}</p>
                <p class="text-danger" v-if="hasValidationErrors && validationErrors.url">{{ validationErrors.url[0] }}</p>
            </div>
        </div>
        <div class="form-group">
            <div class="col-sm-offset-2 col-sm-8">
                <button type="submit" class="btn btn-success">Add</button>&nbsp;&nbsp;
                <router-link to="/repos" class="btn btn-default" exact>Cancel</router-link>
            </div>
        </div>
    </form>
</template>

<script>
    import config from '../../config';

    export default {
        props: [],
        data() {
            return {
                config,
                repoData: {
                    name: '',
                    url: ''
                },
                hasValidationErrors: false,
                validationErrors: [],
                submitted: false
            }
        },
        created() {

        },
        methods: {
            formSubmit() {
                this.$validator.validateAll().then(() => {
                    this.$http.post(this.config.endpoints.postsAddRepo, this.repoData)
                        .then(response => {
                            this.hasValidationErrors = false;

                            return response.json();
                        }, response => {
                            this.hasValidationErrors = true;

                            return response.json();
                        })
                        .then(data => {
                            if (this.hasValidationErrors) {
                                this.validationErrors = data;
                                return;
                            }
                            this.$router.push({name: 'repoList'});
                        });
                }, (data) => {
                    //Validation failed
                });
            }
        }
    }
</script>


